import {Component, ElementRef, Input, OnInit} from '@angular/core';
import {AdaptTranslateService} from '../common/i18n/index';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

@Component({
  selector: 'adapt-login',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.adapt-login]': 'true',
    '[style.display]': '"flex"'
  },
  templateUrl: './login.component.html',
  styles: [`
    .wrapper-login {
      height: auto;
    }
  `]
})

export class AdaptLoginPageComponent extends AdaptRadarAngularGenericDirective implements OnInit {
  element: HTMLElement;
  _productName: string;

  powered: string = 'powered by';

  /**
   * String with classes for setting-up the application logo.
   * When the value is '', the logo will be hidden. For Helix logo use: 'logo-bmc-helix logo-light'
   * @docs-default logo logo-light
   */
  @Input() logoClass: string = 'logo logo-light';

  /**
   * Footer text settings
   * @docs-default
   */
  @Input() footerConfig: LoginFooter;

  /**
   * Product name text
   * @docs-default
   */
  @Input()
  get productName(): string {
    return this._productName;
  }

  set productName(value: string) {
    this._productName = value;
  }

  private _footerConfigDefault: LoginFooter = {
    link: {
      productLinkText: 'www.bmc.com',
      productUrl: '//www.bmc.com'
    }
  };

  constructor(private el: ElementRef,
              private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptLoginPage);

    this.element = el.nativeElement;
    this.powered = ts.getCurrentLanguage()['adapt.login.powered'];
  }

  ngOnInit(): void {
    this.footerConfig = {...this._footerConfigDefault, ...this.footerConfig};
  }
}


export interface LoginFooter {
  /**
   * Sets the version text
   */
  version?: string;
  link?: {
    /**
     * Sets the link text in the footer
     */
    productLinkText: string;
    /**
     * Sets URL to the href in the link
     */
    productUrl: string;
  };
  content?: {
    /**
     * Sets the copyright text of footer
     */
    copyright: string;
    /**
     * Sets the copyright info text of footer
     */
    info: string;
    /**
     * Sets the BMC Helix logo in the footer
     */
    helixLogo?: boolean;
  };
}
